<!--
 * @Author: Marlon
 * @Date: 2020-02-18 10:26:19
 * @LastEditors: Marlon
 * @LastEditTime: 2020-02-21 00:44:55
 * @Description: 表管理
 -->
<template>
  <div class="table-manage">
    <div class="table-manage-edit-area">
      <ul class="table-manage-edit-area-btn">
        <li>
          <el-button type="primary" size="medium" @click="showDialog({type:'新增'})">新增</el-button>
        </li>
      </ul>
      <ul class="table-manage-edit-area-select">
        <li>
          <!-- <label for>选择项目：</label> -->
          <el-select v-model="selectVal" filterable placeholder="请选择项目" size="small">
            <el-option
              v-for="item in selectOpt"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </li>
        <li>
          <!-- <label for>选择数据库：</label> -->
          <el-select v-model="selectVal" filterable placeholder="请选择数据库" size="small">
            <el-option
              v-for="item in selectOpt"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </li>
        <li>
          <el-input placeholder="请输入表名/中文名" v-model="inputVal" clearable size="small"></el-input>
        </li>
        <li>
          <el-button type="primary" size="medium" @click="searchFn">搜索</el-button>
        </li>
      </ul>
    </div>
    <div class="table-manage-table">
      <TableDefault
        :TTitle="tableTitle"
        :TData="tableData"
        :TIsShowPage="true"
        :TEdit="TEdit"
        :TClickItem="TClickItem"
        @editBtnCb="editBtnCb"
      ></TableDefault>
    </div>
    <!-- 弹出框 -->
    <FormDialog
      :FDVisible="FDVisible"
      :FDTitle="FDTitle"
      FDWidth="500"
      :FDForm="FDForm"
      @editBtnCb="editBtnCbDiglog"
    ></FormDialog>
  </div>
</template>

<script>
import mixin from "mixin";

export default {
  mixins: [mixin],
  data() {
    return {
      // 编辑-表格配置
      TEdit: {
        show: true,
        title: "操作",
        editTypes: [
          {
            label: "编辑"
          },
          {
            label: "删除"
          }
        ]
      },
      tableTitle: [
        {
          prop: "name",
          label: "项目名"
        },
        {
          prop: "date",
          label: "创建时间"
        },
        {
          prop: "address",
          label: "字段个数"
        }
      ],
      tableData: [
        {
          date: "2020-02-02 02:02:02",
          name: "通用",
          address: "45"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "海淀",
          address: "45"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "山西",
          address: "45"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "济南",
          address: "45"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "山东",
          address: "45"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "贵州",
          address: "45"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "福州",
          address: "45"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "泰安",
          address: "45"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "南京",
          address: "45"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "广州",
          address: "45"
        }
      ],
      // 是否显示弹出框
      FDVisible: false,
      FDTitle: "",
      // 弹出框配置
      FDForm: [],
      // 可跳转的值
      TClickItem: [
        {
          link: "/",
          cells: ["address"]
        }
      ],
      // 下拉选择
      selectOpt: [
        {
          value: "选项1",
          label: "所有项目"
        },
        {
          value: "选项2",
          label: "通用"
        },
        {
          value: "选项3",
          label: "南京"
        },
        {
          value: "选项4",
          label: "山东"
        },
        {
          value: "选项5",
          label: "济南"
        }
      ],
      selectVal: "",
      // 输入要查询的值
      inputVal: ""
    };
  },
  methods: {
    // 表格-回调
    editBtnCb(editItem) {
     console.log(editItem);
      let text = editItem.label;
      switch (text) {
        case "删除":
          // TODO 发请求
          this.$message({
            type: "success",
            message: "删除成功!"
          });
          return;
        case "编辑":
          this.showDialog({ type: text });
          return;
        case "导入数据库结构":
          this.showDialog({ type: text });
          return;
        case "导入数据标准":
          this.showDialog({ type: text });
          return;
        default:
          break;
      }
    },
    // 弹出框-回调
    editBtnCbDiglog(obj, formReq) {
      this.FDVisible = false;
      if (obj === "确定") {
        // console.log(this.FDTitle);
        switch (this.FDTitle) {
          case "新增":
            // TODO 发请求
            console.log(formReq);
            return;

          default:
            return;
        }
      }
    },
    // 显示弹出框并修改配置
    showDialog({ type }) {
      switch (type) {
        case "新增":
          this.FDTitle = "新增表";
          this.FDVisible = true;
          this.FDForm = [
            {
              type: "select",
              label: {
                name: "项目",
                width: "120"
              },
              width: "200",
              defaultModel: "",
              placeholder: "",
              option: [
                {
                  label: "海淀",
                  value: "haidian"
                }
              ]
            },
            {
              type: "select",
              label: {
                name: "数据库",
                width: "120"
              },
              width: "200",
              defaultModel: "",
              placeholder: "",
              option: [
                {
                  label: "cache",
                  value: "haidian"
                },
                {
                  label: "db",
                  value: "db"
                }
              ]
            },
            {
              type: "input",
              label: {
                name: "数据库名称",
                width: "120"
              },
              width: "280",
              defaultModel: "",
              placeholder: ""
            },
            {
              type: "input",
              label: {
                name: "中文名",
                width: "120"
              },
              width: "280",
              defaultModel: "",
              placeholder: ""
            },
            {
              type: "textarea",
              label: {
                name: "备注",
                width: "120"
              },
              width: "280",
              defaultModel: "",
              placeholder: "",
              autosize: {
                min: 2,
                max: 4
              }
            }
          ];
          return;
        case "编辑":
          this.FDTitle = "编辑表信息";
          this.FDVisible = true;
          this.FDForm = [
            {
              type: "select",
              label: {
                name: "项目",
                width: "120"
              },
              width: "200",
              defaultModel: "",
              placeholder: "",
              option: [
                {
                  label: "海淀",
                  value: "haidian"
                }
              ]
            },
            {
              type: "select",
              label: {
                name: "数据库",
                width: "120"
              },
              width: "200",
              defaultModel: "",
              placeholder: "",
              option: [
                {
                  label: "cache",
                  value: "haidian"
                },
                {
                  label: "db",
                  value: "db"
                }
              ]
            },
            {
              type: "input",
              label: {
                name: "数据库名称",
                width: "120"
              },
              width: "280",
              defaultModel: "",
              placeholder: ""
            },
            {
              type: "input",
              label: {
                name: "中文名",
                width: "120"
              },
              width: "280",
              defaultModel: "",
              placeholder: ""
            },
            {
              type: "textarea",
              label: {
                name: "备注",
                width: "120"
              },
              width: "280",
              defaultModel: "",
              placeholder: "",
              autosize: {
                min: 2,
                max: 4
              }
            }
          ];
          return;
        default:
          return;
      }
    },
    // 选择下拉
    searchFn() {
      // TODO 发请求
    }
  }
};
</script>

<style lang="scss" scoped>
.table-manage {
  &-edit-area {
    overflow: hidden;
    &-btn {
      float: left;
      overflow: hidden;
      li {
        float: left;
        margin: 2px 15px;
      }
    }
    &-select {
      float: left;
      overflow: hidden;
      margin: 4px 55px;
      li {
        float: left;
        margin-right: 20px;
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
  &-table {
    margin: 20px 0;
  }
}
</style>



